.login-page {
  width: 100%;
  &__container {
    padding: 60px 100px;
    > div {
      display: inline-block;
      width: 49.9%;
      text-align: center;
      height: 480px;
      vertical-align: top;
      > div {
        @include vertical-center;
      }
    }
  }
  &__login {
    border-right: 1px solid $black;
    > div {
      padding-right: 100px;      
    }
    &__forgot {
      font-family: 'domaine';
      font-size: 10px;
      color: $black;
      font-style: italic;
      display: block;
      padding: 10px;
    }
    &__form {
      padding: 40px;
    }

  }

  &__signup {
    > div {
      padding-left: 100px;
      padding-bottom: 20px;
      > img {
        padding-top: 40px;
        padding-bottom: 40px;
        height: 80px;
        display: block;
        margin: 0 auto;
        width: 110px;
      }

      > .text {
        display: block;
        margin-bottom: 30px;
      }
    }
  }
}


