.footer {
  height: 180px;
  width: 100%;
  background-color: #332024;
  &__container {
    position: relative;
    height: 100%;
    padding: 0 20px;
  }
  &__copyright {
    font-size: 10px;
    text-align: center;
    color: $white;
    position: absolute;
    bottom: 20px;
    width: calc(100% - 20px);
  }
  &__menu {
    padding-top: 40px;
    display: inline-block;
    vertical-align: top;
    width: 20%;
    > a {
      color: $white;      
      text-decoration: none;
      display: block;
      line-height: 26px;
    }
    &--right {
      float: right;
      text-align: right;
    }
    &--center {
      width: 40%;
      margin-left:10%;

      &__links {
        width: 100%;
        text-align: center;
        :nth-child(2) {
          margin: 0 40px;
        }
        > a {
          color: $white;      
          text-decoration: none;
        }
      }

      &__logos {
        width: 100%;
        text-align: center;
        padding-bottom: 20px;
        :nth-child(2), :nth-child(3) {
          margin-left: 20px;
        }
        :nth-child(3) {
          margin-right: 20px;
        }
        > a {
          color: $white;
          display: inline-block;
          vertical-align: middle;
          text-decoration: none;
        }
      }
      
    }
  }
}


