.landing-page {
	&__section {

		&__container {
			padding-top: 50px;
			padding-bottom: 60px;
			padding-left: 20px;
			padding-right: 20px;
		}

		&__header {
			> span {
				display: inline-block;
				vertical-align: middle;
			}

			> .double-border {
				width: 35%;
			}

			> .header2 {
				text-align: center;
				width: 30%;
			}
		}

		&--splash {
			padding: 0;
			margin-bottom: 60px;
			height: 550px;
			width: 100%;
			background: url('../../images/hero@3x.png') no-repeat 50%;
			background-color: #EDEAEA;
			background-size: contain;
			border-bottom: 1px solid $grey;
			border-top: 1px solid $grey;
			
			&__text {
				width: 50%;
				margin-left: 45%;
				text-align: center;
				@include vertical-center;
				> .tagline {
					margin-bottom: 28px;
				}
				> span {
					display: block;
					margin-bottom: 20px;
				}
			}
		}

		&--shopping {
			&__item {
				width: 29%;
				display: inline-block;
				vertical-align: middle;
				margin: 0 2.16%;
				text-align: center;
				> span {
					display: block;
					padding-top: 8px;
				}
				> img {
					width: 150px;
					cursor: pointer;
				}

				> span {
					margin-top: 30px;
				}

				> a {
					display: block;
					margin-top: 30px;
				}

				&--2{
					margin-left: 1.3%;
				}

				&--top-aligned {
					vertical-align: top;
				}
			}
		}

		&--preorder {
			> div > .double-border {
				width: 30%;
			}
			> div > .header2 {
				width: 40%;
			}

			&__item {
				width: 24%;
				display: inline-block;
				vertical-align: top;
				text-decoration: none;
				color: $black !important;
				> span {
					display: block;
					padding-top: 8px;
				}
				> img {
					max-width: 100%;
					cursor: pointer;
					&:hover {
						border: 1px solid $black;
					}
				}

				&--2, &--3 {
					margin-left: 1.3%;
				}

				&--3 {
					margin-right: 1.3%;
				}
			}
		}

		&--collection {
			> div > .double-border {
				width: 30%;
			}
			> div > .header2 {
				width: 40%;
			}
			> div > iframe {
				width: 100%;
				height: 600px;
			}
		}

		&--mailing {

			> div >  .tagline {
				display: block;
				width: 60%;
				text-align: center;
				margin: 0 auto;
			}

		 	&__form--inline {
				 text-align: center;
				 margin: 30px 0;
				 > input {
					 display: inline-block;
					 height: 36px;
					 width: 300px;
					 padding-left: 10px;
					 vertical-align: middle;
					 border: 1px solid $black;
				 }
				.button {
					display: inline-block;
					vertical-align: middle;
				}
			}
		}
	}
}



