.browse-page {
  width: 100%;  
  &__container {
    padding: 30px;
  }
  &__header {
    width: 75%;
    margin: 30px 0;
    margin-left: 25%;
    &--left {
      display: inline-block;
      vertical-align: top;
    }
    &--right {
      display: inline-block;
      vertical-align: top;
      width: 60%;
      float: right;
      text-align: right;
      > span {
        margin: 0 10px;
      }
    }
  }
  &__footer {
    display: block;
    width: 75%;
    margin: 30px 0;
    margin-left: 25%;
    text-align: right;
    > span {
      margin: 0 10px;
    }
  }
  &__filters {
    display: inline-block;
    vertical-align: top;
    width: 20%;
    margin-right: 5%;

    &__items {
      margin-bottom: 30px;
      > span {
        display: block;
        line-height: 40px;

        > input[type=radio] {
          margin-right: 10px;
        }
      }
      &--header {
        font-size: 24px;
      }
    }
  }
  &__items {
    &__container {
      display: inline-block;
      vertical-align: top;
      width: 75%;
    }
    &__row {
      margin-bottom: 50px;
      &__item {
				width: 32%;
				display: inline-block;
				vertical-align: top;
				text-decoration: none;
				color: $black !important;
				> span {
					display: block;
					padding-top: 8px;
				}
				> img {
					max-width: 100%;
					cursor: pointer;
					&:hover {
						border: 1px solid $black;
					}
				}

				&--2 {
					margin-left: 1.5%;
					margin-right: 1.5%;
        }
      }
    }

  }
}


