@import 'reset';
@import 'variables';
@import 'mixins';
@import 'fonts';


html, body {
  margin: 0;
  padding: 0;
  min-height: 100%;
}

body {
  background: url('../../images/paint@2x.png') no-repeat;
  background-size: cover;
  background-color: white;
}

.button {
  text-decoration: none;
  background-color: #332024;
  color: #f4f4f4;
  font-size: 10px !important;
  box-sizing: border-box;
  display: block;
  min-width: 140px !important;
  height: 40px !important;
  max-width: 180px !important;
  line-height: 40px;
  margin: 0 auto;
  text-align: center;
  outline: none;
  border: none;
  cursor: pointer;

  &--gold {
    background-color: $gold;
  }

  &--line {
    line-height: 1.15rem;    
  }
}

.double-border {
  width: 100%;
  height: 4px;
  border-top: 1px solid $black;
  border-bottom: 1px solid $black;
}

.form {
  &__label {
    display: block;
    width: 100%;
    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: left;
    letter-spacing: 1px;
    padding-bottom: 5px;
  }
  &__input {
    width: 100%;
    display: block;
    height: 36px;
    padding-left: 10px;
    border: 1px solid $black;
    margin-bottom: 20px;
  }
  > input[type='password'] {
    margin-bottom: 10px;
  }
  textarea {
    resize: none;
    height: 200px;
  }
}

select {
  text-transform: none;
  height: 35PX;
  width: 150px;
  background-color: white;
  border-radius: 0px;
  border:0;
  outline: 1px solid $black;
  margin: 10px 0;
  outline-offset:-1px;
}
//Import Component Styles

@import'components/header.scss';
@import'components/footer.scss';
@import'components/landing.scss';
@import'components/browse.scss';
@import'components/login.scss';
@import'components/product.scss';
@import'components/order.scss';
@import'components/quiz.scss';
@import'components/auth.scss';



