.header {
  height: 80px;
  width: 100%;
  line-height: 80px;
  position: relative;
  background: url('../../images/paint@2x.png') no-repeat;
  background-size: cover;
  &__container {
    height: 100%;
    padding: 0 20px;    
  }
  &__logo {
    vertical-align: middle;
    display: inline-block;
    margin-right: 15px;
    height: 100%;
    > span  > img {
      width: 40px;
      line-height: 80px;
      height: 40px;
      display: inline-block;
      vertical-align: middle;
    }
    > span > .navigation-text {
      margin-left: 10px;
      font-size: 16px;
      text-decoration: none;
      display: inline-block;
      vertical-align: middle;
      letter-spacing: 2px;
      color: #635e5e;
    }
  }

  &__menu {
    display: inline-block;
    vertical-align: middle;
    > a {
      text-decoration: none;
      margin: 0 15px;
    }

    &--right {
      float: right;
    }
    &--basket-icon {
      vertical-align: middle;
      padding-right: 5px;
      margin-top: -5px;
    }
  }
}


