.quiz-page {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  background: url('../../images/paint@2x.png') no-repeat;
  background-size: cover;
  background-color: white;

  &__header {
    padding: 0 15%;
    height: 50px;
    line-height: 50px;
    position: relative;
    > span, a{
      display: inline-block;
      vertical-align: middle;
      width: 120px;
    }
    &--left {
      width: 120px;
      cursor: pointer;
    }
    &--center {
      left: calc(50% - 60px);
      position: absolute;
      text-align: center;
      > span {
        font-style: italic;
        text-transform: lowercase;
        padding: 0 10px;
      }
    }
    &--right {
      text-decoration: none;
      color: $black;
      float: right;
      text-align: right;
      cursor: pointer;
    }
  }

  &__content {
    min-width: 60%;
    max-width: 80%;
    text-align: center;
    margin: 0 auto;
    margin-top: 30px;
  }

  &__step {
    &__row {
      margin-bottom: 20px;
      > span {
        display: inline-block;
        border: 1px solid $black;
        height: 40px;
        width: 150px;
        line-height: 40px;
        cursor: pointer;
        border: 1px solid $black;
        &:hover {
          border: 2px solid $black;
        }    
      }
      > div {
        display: inline-block;
        border: 1px solid $black;
        height: 280px;
        width: 190px;
        line-height: 40px;
        cursor: pointer;
        &:hover {
          border: 2px solid $black;
        }

        >img {
          height: 220px;
          width: 150px;
          margin: 0 auto !important;
          display: block;
        }
      }
      :nth-child(2) {
        margin-left: 20px;
        margin-right: 20px;
      }
      :nth-child(3) {
        margin-right: 20px;
      }
    }

    &__header {
      display: block;
      margin-bottom: 30px;
    }
    &__content {
      margin-bottom: 30px;

      &__format {
        margin-bottom: 30px;
        > span {
          display: inline-block;
          width: 100px;
          > input[type='radio'] {
            margin-right: 10px;
          }
        }
      }

      &__select {
        width: 40%;
        text-align: left;
        margin: 0 auto;
        text-align: center;
        > .text--small--bold {
          text-align: center;
        }
        &--twin {
          width: 320px;
          display: block;
          margin: 0 auto;
          > select {
            display: inline-block;
          }
          :nth-child(1) {
            float: left;
          }
          :nth-child(2) {
            float: right;
          }
        }
      }
    }

    &--1 {
      &__sizing {
        > span {
          text-decoration: underline;
          cursor: pointer;
        }
      }
      &__content {
        >img {
          display: block;
          width: 340px;
          height: 380px;
          margin: 0 auto;
          margin-top: 30px;
        }
      }
    }

    &--6 {
      &__content {
        width: 60%;
        margin: 0 auto;
        &--segment {
          text-align: left !important;
          >div {
            margin: 10px 0;
          }
          .text--small {
            line-height: 14px;
            text-transform: none;
          }
        }
      }
    }

    &--7 {
      &__content {
        .quiz-page__step__row:nth-child(3) {
          > span {
            width: 320px;
            margin-left: -20px;
          }
        }
      }
    }

    &--8 {
      &__content {
        .form {
          width: 60%;
          margin: 0 auto;
          height: 260px;
          &--inline {
            > div {
              width: 47%;
              display: inline-block;
              vertical-align: top;
              float: left;
            }
            &--right {
              float: right !important;
            }
          }
        }
      }
    }
  }
}


