.auth-page {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  background: url('../../images/paint@2x.png') no-repeat;
  background-size: cover;
  background-color: white;
  text-align: center;

  .authheader{
    margin-top: 10em;
    width: 100%;
  }

  .logo{
    display: block;
    width: 5em;
    height: 5em;
    margin-left: 47.5%;
    padding-bottom: 2em;
  }

  .content{
    margin-top: 2em;
  }

  .form {
    margin-top: 2em;
    margin-left: 40%;
    width: 20%;
  }
}


