.product-page {
  width: 100%;  
  margin-bottom: 80px;
  
  &__container {
    padding: 30px;
    > div {
      padding-top: 40px;
      display: inline-block;
      vertical-align: top;
    }
  }

  &__back {
    width: 100%;
    float: left;
    text-decoration: none;
  }

  &__img {
    width: 55%;
    > div {
      padding-right: 30px;

      > div {
        display: inline-block;
        vertical-align: top;
      }
    }
    &--train {
      width: calc(20% - 30px);
      float: left;
      > img {
        display: block;
        max-width: 100%;
        max-height: 120px;
        margin-bottom: 20px;
        cursor: pointer;
        &:hover {
          border: 1px solid $black;
        }
      }
    }
    &--main {
      width: 80%;
      height: 620px;
      float: right;
      > img {
        max-width: 100%;
        max-height: 100%;
      }
    }
  }

  &__description {
    width: 45%;
    > div {
      padding-left: 30px;
      > span {
        display: block;
        padding: 5px 0;
      }
      > div {
        margin: 20px 0;
        width: 100%;
        > span {
          display: inline-block;
          vertical-align: middle;
        }
      }
      > .designer-name {
        font-weight: 400;
      }
    }

    &--price {
      font-size: 18px;
      padding-right: 20px;
    }

    &--sale {
      border: 1px solid $black;
      opacity: 0.69;
      font-size: 8px;
      padding: 3px 15px;
    }
    &__buttons {
      text-align: left;
      > .button {
        display: inline-block;
        line-height: 4em;
      }
      :nth-child(1) {
        margin-right: 20px;
      }
    }
  }
}


