.order-page {
  width: 100%;  
  margin-bottom: 100px;
  &__container {
    padding: 30px 120px;
    > div {
      padding-top: 40px;
      display: inline-block;
      vertical-align: top;
    }
  }

  &__back {
    width: 100%;
    float: left;
    text-decoration: none;
  }

  &__payment {
    width: 40%;
    margin-right: 16%;
    > div {
      padding-right: 30px;
      > .button {
        float: left;
      }
    } 
    &--step {
      margin-bottom: 40px;
      > .header1--mobile {
        font-size: 20px;
        line-height: 30px;
      }
      > div  {
        input[type='radio'] {
          margin-top: 20px;
          margin-left: 20px;
          margin-right: 10px;
        }
      }
      .form {
        margin: 20px 0;
       &__inline-container {
         width: 100%;
          > div {
            display: inline-block;
            vertical-align: top;
            width: 28%;
            > input {
              margin-left:  0 !important;
              margin-right:  0 !important;
            }
          }
          :nth-child(2) {
            margin: 0 8%;
          }
        }
      }
    }
  }

  &__summary {
    width: 44%;
    > div {
      padding: 20px;
      border: 1px solid #979797;
      > .header2 {
        font-size: 14px;
        color: #474747;
      }
    }
    &__bill {
      margin-top: 60px;
      width: 50%;
      margin-left: 50%;
      >span {
        display: block;
        text-align: right;
        font-size: 14px;
        line-height: 25px;
      }
    }
    &--item {
      margin-top: 40px;
      >img {
        width: 30%;
        display: inline-block;
        vertical-align: top;
        padding-right: 30px;
      }
      &__details {
        display: inline-block;
        vertical-align: top;
        width: calc(70% - 30px);
        > span {
          display: block;
          padding: 5px 0;
        }
        > .designer-name {
          font-weight: 400;
        }
        > .header1-mobile {
          font-size: 24px;
        }
      }
    }
  }
}


.order-success-page {
  width: 100%;
  min-height: calc(100vh - 260px);
  &__container{
    padding: 120px 240px;
    > div {
      border: 1px solid $black;
      text-align: center;
      > span {
        margin: 30px 0px;
        display: block;
      }
      > .button {
        margin: 30px auto;
      }
    }
  }  
}


