//Monsterrat

@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700');

@font-face {
  font-family: 'domaine';
  src: url('./fonts/DomaineTextTest-Light.woff') format('woff');
}

.header0 {
  font-family: 'Montserrat', sans-serif;
  font-size: 50px;
  font-weight: 400;
  text-transform: uppercase;
  color: $black;
}

.header1, .header1--mobile{
  font-size: 68px;
  font-weight: 300;
  font-family: 'domaine';
}

.header1--mobile {
  font-size: 30px;
}

.header2, .navigation-text{
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  color: $black;
}

.navigation-text {
  font-size: 12px;
  color: $black;
  letter-spacing: 1px;
}

.tagline {
  color: #484848;
  font-size: 16px;
  font-family: 'domaine';  
}

.text {
  color: $black;
  font-size: 14px;
  font-family: 'domaine';  
}

.text--small, .text--small--bold {
  font-family: 'Montserrat', sans-serif;
  font-size: 10px;
  font-weight: 400;
  text-transform: uppercase;
  color: $black;
}

.text--small--bold {
  font-size: 11px;
  font-weight: 700;
}

.designer-name, .price {
  font-family: 'Montserrat', sans-serif;
  color: $black;
  font-weight: 700;
  font-size: 12px;
  letter-spacing: 2px;
}

.product-name {
  font-family: 'domaine';
  font-size: 13px;
}


